<template>

  <div class="container-fluid bg-light">
    <div class="row">
      <!-- 左邊按鈕選項 -->
      <div class="col-3 col-xl-2 mt-3">
        <nav class="nav flex-column">
          <router-link class="nav-link text-dark" to="news">焦點新聞</router-link>
          <router-link class="nav-link text-dark" to="business">商業</router-link>
          <router-link class="nav-link text-dark" to="international">國際</router-link>
          <router-link class="nav-link text-dark" to="disabled">Disabled</router-link>
        </nav>
      </div>

      <div class="col-9 col-xl-6">
        <main class="border rounded-circle-20 mt-2 p-3" v-for="newsdata in mynews" :key="newsdata">
          <img :src="newsdata.urlToImage" alt="images" width="300px" height="200px">
          <a :href="newsdata.url">
            <h3 class="mt-2">{{newsdata.title}}</h3>
          </a>
        </main>
      </div>
    </div>
  </div>

</template>

<script>
import axios from 'axios'
export default {
  name: "news",
  data() {
    return {
      mynews: []
    }
  },
  mounted() {
    axios.get("https://newsapi.org/v2/top-headlines?country=tw&apiKey=108aa75e7444469cbbbaea5ee72857ab").then((res) => {
      let self = this
      self.mynews = res.data.articles
      console.log(res.data.articles)
    }).catch((err) => {
      console.error(err)
    });
  }
}
</script>



