<template>

  <div class="container-fluid bg-light">
    <div class="row">
      <asides />
      <div class="col-9 col-xl-6">
        <main class="border mt-2 p-3 rounded-circle-20" v-for="(newsdata,index) in mynews" :key="index">
          <img :src="newsdata.urlToImage" alt="images" width="300px" height="200px">
          <a :href="newsdata.url">
            <h3 class="mt-2">{{newsdata.title}}</h3>
          </a>
        </main>
      </div>
    </div>
  </div>

</template>

<script>
import axios from 'axios'
import asides from "./asides"  //側邊選單

export default {
  name: "business",
  data() {
    return {
      mynews: []
    }
  },
  mounted() {
    axios.get("https://newsapi.org/v2/top-headlines?country=tw&category=business&apiKey=108aa75e7444469cbbbaea5ee72857ab").then((res) => {
      let self = this
      self.mynews = res.data.articles
      // console.log(res.data.articles)
    }).catch((err) => {
      console.error(err)
    });
  },
  components: {
    asides

  }

}
</script>



